* {
  margin: 0px;
  padding: 0px;
  font-family: 'Source Sans Pro', sans-serif;
  color: rgb(49, 49, 49);
  font-size: 16px;
  scroll-behavior: smooth;
  letter-spacing: 1.5px;
  line-height: 2em;
  background: rgb(248, 248, 248);
}

* h1 {
  font-family: 'Josefin Slab', serif;
  font-size: 2em;
  font-weight: 1500;
}

a {
  text-decoration: none;
}

.text-deco-none {
  text-decoration: none;
}

.main {
  margin: 0;
  transition: 0.5s;
}

.header {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgb(248, 248, 248);
  flex-wrap: wrap;
  z-index: 1;
}

.title {
  padding-left: 20px;
}

.title-h1 {
  padding: 10px 0px 10px 0px;
}

.navigation ul {
  display: flex;
  list-style: none;
}

.navigation ul li {
  padding: 26px 15px 26px 15px;
  transition: 0.5s;
  cursor: pointer;
}

.navigation ul li:hover {
  background-color: rgb(221, 221, 221);
}

.splash {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60vh;
  background-color: rgb(248, 248, 248);
}

.desc {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 50px;
  margin: auto;
  width: 90%;
  text-align: center;
}

.desc h1 {
  font-size: 2em;
}

.desc p {
  font-size: 1.2em;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px 5px 20px 5px;
  background-color: rgb(248, 248, 248);
}

.card {
  display: block;
  position: relative;
  overflow: hidden;
  height: 320px;
  width: 600px;
  border: transparent;
  border-radius: 2px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  background: rgb(248, 248, 248);
  margin: 5px;
  transition: 0.5s;
  cursor: pointer;
  z-index: 0;
}

.card:hover {
  transform: scale(1.05);
}

.card img {
  margin: auto;
  position: relative;
  width: 100%;
  transition: 0.5s;
}

.contact {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 30vh;
}

.iconGroup {
  display: flex;
  justify-self: center;
  align-items: center;
}

.icon {
  padding: 10px;
}

.icon img {
  width: 30px;
}

.footer {
  text-align: center;
  padding-bottom: 10px;
}

.footer p {
  line-height: 1em;
  font-size: 0.6em;
  color: rgb(170, 170, 170);
}

.footer a p {
  line-height: 1em;
  font-size: 0.6em;
  color: rgb(170, 170, 170);
}

.modal-img {
  max-width: 800px;
}

.button {
  color: rgb(170, 170, 170);
  cursor: pointer;
  font-size: 1.3em;
}

.close-btn {
  margin-left: auto;
  margin-right: 5px;
  margin-bottom: 5px;
  padding: 10px;
}

.ReactModal__Overlay {
  opacity: 0;
  transition: all 500ms;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

.modal-description{
  max-height: 200px;
  overflow: auto;
}

.about {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-right: 25px;
  margin-left: 25px;
}

.switch-wrapper {
  position: relative;
}
 
.switch-wrapper > div {
  position: relative;
}

@media only screen and (max-width: 600px) {
  * {
    font-size: 12px;
  }
  .card {
    width: 100%;
    height: 150px;
    margin: 3px;
    border-radius: 1px;
  }
  .modal-img {
    max-width: 320px;
  }
  .card img {
    position: relative;
    bottom: 50px;
  }
}

@media only screen and (max-width: 1026px) {
  * {
    font-size: 12px;
  }
  .header {
    justify-content: center;
  }
  .splash {
    padding-top: 100px;
  }
  .title {
    padding-left: 0px;
    padding-top: 10px;
  }
  .navigation ul li {
    padding: 15px;
    transition: 0.5s;
    cursor: pointer;
  }
}